import { useState } from "react";
import { useTranslation } from "react-i18next";
import { specificFormatDate } from "../../../../../utils/date_convert";
import { useDepartmentCreatedHoliday } from "../../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
import AddHolidayCalendar from "./AddHolidayCalendar";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormTextarea from "../../../../Ui/Input/FormTextarea";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import style from "../../../FinanceManager/Income/Income.module.css";
const AddHoliday = ({ did, onClose, allHoliday, onRefetch }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(0);
  const [arr, setArr] = useState([]);
  const [holidayDate, setHolidayDate] = useState([]);
  const [reason, setReason] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [departmentCreatedHoliday] = useDepartmentCreatedHoliday();
  const removeDateArray = (arr) => {
    setHolidayDate(arr);
    setArr(arr);
  };

  const addDateArray = (date) => {
    const curr = [...holidayDate];
    curr?.push(date);
    setHolidayDate(curr);
    setArr((prev) => [...prev, date]);
  };

  const submitHandler = () => {
    if (did && reason) {
      setDisabled(true);
      const newFormatDate = specificFormatDate(holidayDate);
      departmentCreatedHoliday({
        did: did,
        holiday: {
          reason: reason,
          dates: newFormatDate,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled(false);
          onClose("");
        })
        .catch(() => {
          setDisabled(false);
        });
      setArr([]);
    }
  };

  return (
    <>
      {step === 0 ? (
        <AddHolidayCalendar
          onClose={onClose}
          arr={arr}
          removeDateArray={removeDateArray}
          addDateArray={addDateArray}
          allHoliday={allHoliday}
          setStep={setStep}
        />
      ) : step === 1 ? (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_leave_reason")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={() => setStep(0)}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormTextarea
                label={t("reason")}
                placeholder={t("reason")}
                name="reason"
                value={reason}
                type={"text"}
                onChange={(e) => setReason(e.target.value)}
              />

              <CreateButton label="create" onAction={submitHandler} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      ) : null}
    </>
  );
};

export default AddHoliday;
