import { useEffect, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../Ui/Button/CreateButton";
import DepartmentHolidayCard from "./DepartmentHolidayCard";
import { useDepartmentAllHoliday } from "../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import DepartmentHolidayCalendar from "./DepartmentHolidayCalendar";
import AddHoliday from "./AddHoliday/AddHoliday";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../../hooks/authentication-api";
import { useAutomateInstituteDepartmentHolidayExcel } from "../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
const DepartmentHoliday = ({ did }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView();
  const [allHoliday, setAllHoliday] = useState([]);
  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteDepartmentHolidayExcel] =
    useAutomateInstituteDepartmentHolidayExcel();

  const {
    departmentAllHoliday,
    departmentAllHolidayLoading,
    departmentAllHolidayRefetch,
  } = useDepartmentAllHoliday({
    data: {
      did: did,
      page: page,
      limit: 10,
    },
    skip: !did,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (departmentAllHoliday?.list?.length) {
      const arr = [];
      departmentAllHoliday?.list?.map((arrVal) =>
        arrVal?.dDate?.map((dat) => {
          const val = dat?.split("/");
          const day = val[0] > 9 ? val[0] : `${val[0]?.slice(1, 2)}`;
          const month = val[1] > 9 ? val[1] : `${val[1]?.slice(1, 2)}`;
          arr.push(`${day}-${month}-${val[2]}`);
          return "";
        })
      );
      setAllHoliday(arr);
      let obj = nextPage(departmentAllHoliday?.list?.length);
      setState(() => obj);
    }
  }, [departmentAllHoliday?.list?.length]);

  const onClose = () => {
    setStep("");
  };

  const onRefetch = () => {
    setPage((pre) => (pre = 1));
    departmentAllHolidayRefetch();
  };
  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && did) {
            automateInstituteDepartmentHolidayExcel({
              did: did,
              excel_file: res.data?.imageKey,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  //   console.info("sdffgs", did);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("holiday_list")}</h6>
            <div className={style.fm_search}>
              <img
                src={`${assestsUtilityUrl}/calendar.svg`}
                alt="calendar icon"
                onClick={() => setStep(1)}
              />
              <CreateButton
                label="add_new_holiday"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setStep(2)}
              />
              <UploadExcelButton
                onExcelFileUpload={onExcelFileUpload}
                label={t("holiday_excel")}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {departmentAllHoliday?.list?.map((holiday, index) =>
            departmentAllHoliday?.list?.length === index + 1 ? (
              <div key={holiday?._id} ref={ref}>
                <DepartmentHolidayCard
                  holiday={holiday}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <DepartmentHolidayCard
                holiday={holiday}
                key={holiday?._id}
                onRefetch={onRefetch}
              />
            )
          )}
          {departmentAllHolidayLoading ? (
            <Loading />
          ) : (
            departmentAllHoliday?.list?.length === 0 && (
              <EmptyMessage title="No holiday created." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {step === 1 ? (
        <DepartmentHolidayCalendar onClose={onClose} allHoliday={allHoliday} />
      ) : step === 2 ? (
        <AddHoliday
          onClose={onClose}
          allHoliday={allHoliday}
          did={did}
          onRefetch={onRefetch}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default DepartmentHoliday;
