import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "./Button.module.css";
const UploadExcelButton = ({
  onExcelFileUpload,
  acceptType,
  label,
  dynamicKey,
  title,
  assestUrl,
  customStyle,
  customStyleLabel,
  dummyExcel,
}) => {
  const { t } = useTranslation();
  return (
    <>
      {dummyExcel ? (
        <label className={style.upload_excel_file} style={customStyle}>
          <img
            src={
              assestUrl ? assestUrl : `${assestsUserFinanceUrl}/upload-xslx.svg`
            }
            alt="add student icon"
            title={title ?? "Upload excel file"}
          />
          {label && <h6 style={customStyleLabel}>{t(label)}</h6>}
        </label>
      ) : (
        <label
          className={style.upload_excel_file}
          htmlFor={dynamicKey ?? "uploadExcel"}
          style={customStyle}
        >
          <img
            src={
              assestUrl ? assestUrl : `${assestsUserFinanceUrl}/upload-xslx.svg`
            }
            alt="add student icon"
            title={title ?? "Upload excel file"}
          />
          {label && <h6 style={customStyleLabel}>{t(label)}</h6>}
        </label>
      )}

      <input
        type={"file"}
        id={dynamicKey ?? "uploadExcel"}
        accept={acceptType}
        onChange={onExcelFileUpload}
        style={{
          display: "none",
        }}
      />
    </>
  );
};

export default UploadExcelButton;
