import dayjs from "dayjs";
import {
  assestsUserFinanceUrl,
  assestsUtilityUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../DepartmentManager.module.css";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import DepartmentHolidayCardMenu from "./DepartmentHolidayCardMenu";

const DepartmentHolidayCard = ({ holiday, onRefetch }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState("");

  return (
    <>
      <div className={style.dm_card}>
        <img
          src={`${assestsUtilityUrl}/holiday.svg`}
          alt="holiday icon"
          className={style.dm_image}
        />
        <div className={style.dm_card_inner}>
          <h6>
            {" "}
            {t("dates")}
            {": "}
            {holiday?.dDate.map((date, index) =>
              holiday?.dDate?.length === index + 1 ? (
                <span key={index}>{date?.slice(0, 2)} </span>
              ) : (
                <span key={index}>{date?.slice(0, 2)}, </span>
              )
            )}
            {holiday?.dDate ? (
              <span>{dayjs(holiday?.dDate[-1]).format("MMM YYYY")}</span>
            ) : null}
          </h6>
          <p> {holiday?.dHolidayReason ?? ""}</p>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={() => setOpen(true)}
          className={style.menu_icon}
          title="Menu"
        />
      </div>
      {open && (
        <DepartmentHolidayCardMenu
          hid={holiday?._id}
          onRefetch={onRefetch}
          onClose={() => setOpen("")}
        />
      )}
    </>
  );
};

export default DepartmentHolidayCard;
