import { useTranslation } from "react-i18next";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useDepartmentDeleteHoliday } from "../../../Hooks/ApiHooks/DepartmentManager/department-manager-api";

const DepartmentHolidayCardMenu = ({ onClose, hid, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [departmentDeleteHoliday] = useDepartmentDeleteHoliday();
  const onRemove = () => {
    if (hid) {
      setDisabled((pre) => !pre);
      departmentDeleteHoliday({
        hid: hid,
      })
        .then(() => {
          onRefetch();
        })
        .catch({});
    }
  };
  return (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onRemove}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default DepartmentHolidayCardMenu;
